import { ModuleWithProviders, NgModule } from '@angular/core';
import { hookDeviceGridAction } from '@c8y/ngx-components/device-grid';
import { gettext, hookNavigator, hookRoute, ViewContext } from '@c8y/ngx-components';
import {
  Lwm2mBootstrapParametersGuard,
  Lwm2mMicroserviceSubscribedGuard
} from '@c8y/ngx-components/protocol-lwm2m/guards';
import {
  Lwm2mDeviceGridFactory,
  LWM2MPostOpertaionsNavigationFactory
} from '@c8y/ngx-components/protocol-lwm2m/factories';

@NgModule()
export class Lwm2mModule {
  static forRoot(): ModuleWithProviders<Lwm2mModule> {
    return {
      ngModule: Lwm2mModule,
      providers: [
        Lwm2mMicroserviceSubscribedGuard,
        Lwm2mBootstrapParametersGuard,
        hookNavigator(LWM2MPostOpertaionsNavigationFactory),
        hookDeviceGridAction(Lwm2mDeviceGridFactory),
        hookRoute({
          path: 'post-operations',
          loadComponent: () =>
            import(
              '@c8y/ngx-components/protocol-lwm2m/components/post-registration-operations'
            ).then(m => m.LWM2MPostOperationsComponent),
          icon: 'terminal',
          priority: 2000,
          canActivate: [Lwm2mMicroserviceSubscribedGuard]
        }),
        hookRoute({
          path: 'lwm2m-bootstrap-parameters',
          context: ViewContext.Device,
          loadComponent: () =>
            import('@c8y/ngx-components/protocol-lwm2m/components/bootstrap-parameters').then(
              m => m.Lwm2mBootstrapParametersComponent
            ),
          label: gettext('LWM2M bootstrap parameters'),
          icon: 'c8y-device-protocols',
          canActivate: [Lwm2mBootstrapParametersGuard]
        })
      ]
    };
  }
}
